@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Kaushan+Script&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background: white url(/static/media/watercolor.96b5713a.png);
    background-attachment: fixed;
}
header{
    background-color: rgba(255, 255, 255, 0.699);
}
nav{
    display: inline-flex;
    float: right;
}
nav > a{
    color: black;
    text-decoration: none;
    text-transform: capitalize;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    padding: 1em;
}
nav > a:hover{
    background-color: rgba(0, 0, 0, 0.068);
}
header img{
    height: 72px;
    display: inline-block;
}
@media only screen and (max-width: 300px) {
    header a{
        display: block;
        border-bottom: 2px double black;
    }
}
.banner{
    padding-top: 1rem;
    text-align: center;
    background-color: rgba(0, 255, 255, 0.342);
}
.banner>h1{
    font-size: 1.6rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-transform: uppercase;
}
.banner h4{
    font-family: 'Dancing Script';
    font-size: 2rem;
}
#waBtn{
    display: inline-block;
    border: 2px solid black;
    border-radius: 4px;
    margin: 1em;
    background-color:rgba(0, 128, 0, 0.562);
    color: rgba(255, 255, 255, 0.842);
    font-size: 1rem;
    font-family: 'Times New Roman', Times, serif;
    font-weight: bold;
    text-decoration: none;
    padding: .4rem;
}
#waBtn>img{
    height: 1rem;
}
.figContain{
    border-top: 10px dashed black;
}
.figContain figure{
    width: 100%;
    height: 60vh;
}
#lfig{
    background-image: url(/static/media/bg-1.a898350c.jpg);
    background-repeat: no-repeat;
    background-position: center middle;
    background-size: cover;
}
#rfig{
    background-image: url(/static/media/bg-2.6da3ca91.jpg);
    background-repeat: no-repeat;
    background-position: center middle;
    background-size: cover;
    background-attachment: fixed;
}












