section{
    background-color: white;
    margin: 1rem;
    padding-bottom: 2rem;
}
section>h3{

}
section>div{
    padding: 1rem;
}
section>div::before{
    content: '“';
    font-size: 2rem;
    font-weight: bold;
}
section>div::after{
    content: '„';
    font-size: 2rem;
    font-weight: bold;
}
section .auther{
    float: right;
    margin: 1rem;
}
section .auther::before{
    content: '--';
}




















